.app {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .logo{
    position: fixed;
    right: -5%;
    top: -10rem
  }
  
  .loginforms {
    /*width: 100%;
    max-width: 600px; /* Adjust the width as needed */
      width: 100%;
      max-width: 600px; /* Adjust the width as needed */
      /*box-shadow: 5px 5px 10px 2px rgba(95, 95, 95, 0.24);*/
      padding: 2rem;
      position: absolute;
      top: 10em
  }
  
  .inputcontainers {
    width: calc(50% - 10px); /* 50% width for each container with some spacing */
    margin-bottom: 20px;
    display: inline-block;
    vertical-align: top;
  }
  
  .inputcontainers:nth-child(odd) {
    margin-right: 20px; /* Add some space between input containers */
  }
  
  .inputcontainers:last-child {
    margin-right: 0; /* Remove margin from the last container */
  }
  
  .inputcontainers label {
    display: block;
    margin-bottom: 5px;
  }
  
  .inputcontainers input {
    width: 100%;
    padding: 2px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .buttoncontainers {
    text-align: center;
  }
  
  .buttons {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 20px;
    width: 30%;
    border-radius: 10px;
  }

  .buttons:hover{
    border-radius: 10px;
  }
  
  .errmsg {
    color: red;
  }
  
  .button28 {
    appearance: none;
    background-color: transparent;
    border: 2px solid #1A1A1A;
    border-radius: 15px;
    box-sizing: border-box;
    color: #3B3B3B;
    cursor: pointer;
    display: inline-block;
    font-family: Roobert,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
    font-size: 16px;
    font-weight: 600;
    line-height: normal;
    margin: 0;
    min-height: 60px;
    min-width: 0;
    outline: none;
    padding: 16px 24px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: 9%;
    will-change: transform;
  }
  
  .button28:disabled {
    pointer-events: none;
  }
  
  .button28:hover {
    color: #fff;
    background-image: linear-gradient(92.83deg, #ff7426 0, #f93a13 100%);
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    transform: translateY(-2px);
    border: none;
  }
  
  .button28:active {
    box-shadow: none;
    transform: translateY(0);
  }
  
      
  .back_button .button28{
    position: absolute;
    left: 5%;
    top: 1em
  }